export const schoolIcon = (school, attrs) => {
  switch (school) {
    case Schools.CHRONOMANCER:
      return <Hourglass />;
    case Schools.ELEMENTALIST:
      return <ElementalBlast />;
    case Schools.ENCHANTER:
      return <EnchantedWeapon />;
    case Schools.ILLUSIONIST:
      return <Illusion />;
    case Schools.NECROMANCER:
      return <SkullBook />;
    case Schools.SIGILIST:
      return <SpellBook />;
    case Schools.SOOTHSAYER:
      return <CrystalBall />;
    case Schools.SUMMONER:
      return <Heptagram />;
    case Schools.THAUMATURGE:
      return <SunPraise />;
    case Schools.WITCH:
      return <WitchHat />;
    default:
      return <div>No icon for {school}.</div>;
  }
};
